<template>
  <div class="card" id="error">
    <div id="title"></div>
    <div id="desc"></div>
    <div id="desc">{{ error }}</div>
    <div id="desc"></div>
  </div>
</template>

<script>
export default {
  props: ['error']
}
</script>


<style scoped>
#error {
  background: rgb(255, 200, 61);
}

#title {
  font-size: 20px;
}

#desc {
  margin-top: 12px;
  font-size: 16px;
}
</style>



